.nav {
  position: fixed;
  top: 70px;
  left: 50px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 10px;
  background-color: white;
}

.nav__btn {
  padding: 30px;
  color: gray;
  font-size: 18px;
  text-decoration: none;
  font-weight: 500;
}

.home {
  border-bottom: 1px solid wheat;
}
