* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.details {
  display: grid;
  grid-template-columns: 1fr 2fr;
  padding: 70px;
  padding-left: 200px;
  width: 100%;
}
.detail__text {
  font-family: "Open Sans";
  display: flex;
  flex-direction: column;
  font-weight: 300;
  width: 80%;
  margin-left: 30px;
}

span {
  margin-bottom: 5px;
}
.detail__title {
  opacity: 0.8;
  font-size: 40px;
  font-weight: 600;
}
.detail__year {
  opacity: 0.5;
  font-size: 30px;
}

img {
  width: 55vh;
}

.detail__genres {
  display: flex;
  list-style: none;
  padding: 0;

  margin-bottom: 50px;
}

.genres {
  padding-right: 10px;
  margin-right: 10px;
  font-size: 17px;
  font-weight: 500;
  border-right: 1px solid gainsboro;
}

.detail__summary {
  opacity: 0.8;
  font-size: 20px;
  line-height: 2;
}
