.about {
  display: flex;
  width: 100%;
  height: 100%;
  text-align: center;
  align-items: center;
  font-size: 50px;
  font-weight: 600;
}

.about span {
  color: gray;
  background-color: white;
  padding: 50px;
  border-radius: 10px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
